// SECTION u-profile :: START

.u-profile {
	overflow: unset;
	padding-top: 120px;
	padding-bottom: 120px;

	.main__bg {
		z-index: -1;

		&-1 {
			@include adaptive(767) {
				position: absolute !important;
				top: 15%;
			}
		}
	}

	&__bg {
		z-index: -1;
		bottom: 0;
		left: 0;
		display: flex;
		align-items: flex-end;
	}

	&__wrapper {
		display: flex;
		@include adaptive(991) {
			flex-wrap: wrap;
		}

		&-left {
			width: 395px;
			padding-right: 30px;
			@include adaptive(1279) {
				width: 255px;
			}
			@include adaptive(991) {
				width: 100%;
				padding-right: 0;
			}
		}

		&-right {
			flex: 1;
			min-width: 1px;
			max-width: 100%;
		}
	}

	&__section {
		width: 100%;
		border-radius: 6px;
		box-shadow: 0 1px 20px 0 rgba(17, 30, 79, 0.1);
		background-color: #ffffff;

		&-header {
			display: flex;
			align-items: center;
			justify-content: space-between;

			.dropdown {
				&-toggle {
					height: 30px;

					.icon-font {
						color: #AEAEAE;
						font-size: 5px;
					}

					&:after {
						display: none;
					}
				}
			}
		}

		&-title {
			font-size: 20px;
			font-weight: 600;
			letter-spacing: 0.47px;
		}
	}

	&__main {
		overflow: hidden;
		position: relative;
		padding-top: 130px;
		@include adaptive(991) {
			padding-top: 200px;
			padding-left: 225px;
		}
		@include adaptive(767) {
			padding-top: 130px;
			padding-left: 0;
		}

		&-bg {
			z-index: 0;
			top: 0;
			left: 0;
			height: 200px;
			background-color: #6772e5;
			background-image: url('../img/img-profile-bg.png');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
		}

		&-body {
			@include adaptive(991) {
				display: flex;
				flex-wrap: wrap;

				> div {
					&:nth-of-type(1) {
						width: 35%;
					}
					&:nth-of-type(2) {
						width: 50%;
						padding-top: 1rem;
					}
					&:nth-of-type(3) {
						width: 100%;
						padding-left: 0 !important;
					}
				}
			}
			@include adaptive(767) {
				display: block;

				> div {
					&:nth-of-type(1),
					&:nth-of-type(2),
					&:nth-of-type(3) {
						width: 100%;
					}
					&:nth-of-type(2) {
						padding-top: 0;
					}
					&:nth-of-type(3) {
						padding-left: 1rem !important;
					}
				}
			}
		}

		&-photo {
			position: absolute;
			top: 15px;
			left: 15px;

			.icon-font {
				color: rgba(#fff, 0.8);
				font-size: 19px;
			}
		}

		&-badge {
			position: absolute;
			top: 15px;
			right: 15px;
			display: flex;
			align-items: center;
			justify-content: center;
			height: 20px;
			padding-left: 10px;
			padding-right: 10px;
			border-radius: 4px;
			background-color: rgba(#000000, 0.3);
			font-size: 10px;
			letter-spacing: 0.38px;
			color: #ffffff;
		}

		&-image {
			position: relative;
			width: 155px;
			height: 155px;
			margin-left: auto;
			margin-right: auto;
			border-radius: 50%;
			background-image: linear-gradient(135deg, #FF5980 0%, #FFA882 100%);
			@include adaptive(991) {
				position: absolute;
				top: 135px;
				left: 50px;
			}
			@include adaptive(767) {
				position: relative;
				top: 0;
				left: 0;
			}

			> div {
				overflow: hidden;
				position: absolute;
				top: 1px;
				left: 1px;
				width: calc(100% - 2px);
				height: calc(100% - 2px);
				border-radius: 50%;
				background-color: #ffffff;
			}

			img {
				@extend %imageResCoverAbs;
			}
		}

		&-name {
			font-size: 20px;
			font-weight: 600;
			letter-spacing: 0.47px;
			text-align: center;
			@include adaptive(991) {
				text-align: left;
			}
			@include adaptive(767) {
				text-align: center;
			}
		}

		&-from {
			font-size: 16px;
			letter-spacing: 0.38px;
			text-align: center;
			@include adaptive(991) {
				text-align: left;
			}
			@include adaptive(767) {
				text-align: center;
			}
		}

		&-stat {
			padding-left: 15px;
			padding-right: 15px;
			@include adaptive(1279) {
				padding-left: 0;
				padding-right: 0;
			}
			@include adaptive(767) {
				justify-content: center;
			}

			> div {
				width: 50%;
				text-align: center;

				@include adaptive(767) {
					width: 35%;
				}
				@include adaptive(374) {
					width: 45%;
				}
			}

			p {
				font-size: 30px;
				font-weight: 600;
				line-height: 0.8;
			}

			span {
				font-size: 14px;
				letter-spacing: 0.33px;
			}
		}

		&-social {
			@include adaptive(1279) {
				flex-wrap: wrap;
			}
			@include adaptive(991) {
				flex-wrap: nowrap;
			}
			@include adaptive(374) {
				flex-wrap: wrap;
			}


			> div {
				width: 100%;
				max-width: calc(100% / 6);
				padding-left: 5px;
				padding-right: 5px;
				@include adaptive(1279) {
					max-width: calc(100% / 3);
					padding-bottom: 15px;
				}
				@include adaptive(991) {
					width: auto;
					max-width: unset;
					padding-bottom: 0;

					&:first-of-type {
						padding-left: 0;
					}
				}
				@include adaptive(767) {
					width: 100%;
					max-width: calc(100% / 6);

					&:first-of-type {
						padding-left: 5px;
					}
				}
				@include adaptive(374) {
					max-width: calc(100% / 3);
					padding-bottom: 15px;
				}
			}

			a {
				width: 45px;
				height: 45px;
				margin-left: auto;
				margin-right: auto;
				border-radius: 50%;
				background-image: linear-gradient(135deg, #FF5980 0%, #FFA882 100%);
				@include adaptive(991) {
					margin-left: 0;
				}
				@include adaptive(767) {
					margin-left: auto;
				}

				.icon-font {
					color: #ffffff;
					font-size: 20px;
				}
			}
		}
	}

	&__about {
		.u-profile__section {
			&-body {
				p {
					font-size: 16px;
					line-height: 1.5;
					letter-spacing: 0.38px;
				}
			}
		}
	}

	&__tweets {
		&-box {
			border-bottom: 1px solid #D8D8D8;

			&:last-of-type {
				border-bottom: none;
			}

			> div {
				&:nth-of-type(1) {
					width: 65px;
				}
				&:nth-of-type(2) {
					flex: 1;
					min-width: 1px;
					max-width: 100%;
				}
			}

			&-wrapper {
				display: flex;
				flex-direction: column;
			}

			&-image {
				overflow: hidden;
				position: relative;
				width: 50px;
				height: 50px;
				border-radius: 50%;

				img {
					@extend %imageResCoverAbs;
				}
			}

			&-title {
				font-size: 15px;
				font-weight: 600;
				line-height: 1.6;
				letter-spacing: 0.35px;

				span {
					padding-left: 15px;
					font-size: 15px;
					font-weight: normal;
					line-height: 1.6;
					letter-spacing: 0.35px;
					color: #657786;
				}
			}

			&-desc {
				font-size: 15px;
				line-height: 1.33;
				letter-spacing: 0.35px;

				a {
					padding-left: 10px;
					color: #1b95e0;
				}
			}
		}
	}

	&__created,
	&__subscribed {
		.u-profile__section {
			&-cover {
				min-height: 313px;
				@include adaptive(767) {
					min-height: unset;
				}
			}

			&-header {
				border-bottom: 1px solid #D8D8D8;
			}
		}

		&--empty {
			.u-profile__created,
			.u-profile__subscribed {
				&-info {
					display: none;
					@include adaptive(767) {
						display: block;
					}

					p {
						padding-top: 105px;
						padding-bottom: 105px;
						text-align: center;
						font-size: 18px;
						font-weight: 500;
						line-height: 1.33;
						letter-spacing: 0.35px;
						color: rgba(#333, 0.6);
					}
				}

				&-table {
					&-body {
						justify-content: center;

						p {
							padding-top: 105px;
							padding-bottom: 105px;
							text-align: center;
							font-size: 18px;
							font-weight: 500;
							line-height: 1.33;
							letter-spacing: 0.35px;
							color: rgba(#333, 0.6);
						}
					}
				}
			}
		}

		&-carousel {
			display: none;
			padding-top: 30px;
			@include adaptive(767) {
				display: block;
			}

			.swiper {
				&-container {
					padding-bottom: 50px;
				}

				&-slide {
					&-title {
						font-size: 22px;
						font-weight: bold;

						span {
							white-space: nowrap;
							font-size: 14px;
							font-weight: normal;
						}
					}

					&-block {
						display: flex;
						flex-wrap: wrap;
						padding-top: 30px;

						> div {
							position: relative;
							display: flex;
							flex-direction: column;
							justify-content: center;
							width: 50%;
							min-height: 125px;
							padding-top: 15px;
							padding-bottom: 15px;

							&:nth-of-type(1),
							&:nth-of-type(2) {
								margin-bottom: 30px;

								&:after {
									content: '';
									position: absolute;
									bottom: -15px;
									left: 0;
									width: 100%;
									height: 1px;
									background-color: #d8d8d8;
								}
							}

							&:nth-of-type(2),
							&:nth-of-type(4) {
								padding-left: 15px;
								border-left: 1px solid #D8D8D8;
							}
						}

						p {
							padding-bottom: 20px;
							font-size: 16px;
							line-height: 1.5;
						}

						span {
							font-size: 25px;
							font-weight: 600;
							line-height: 0.96;
						}
					}
				}
			}
		}

		&-table {
			display: flex;
			flex-direction: column;
			@include adaptive(767) {
				display: none;
			}

			&-head,
			&-body {
				display: flex;
				align-items: center;
				border-bottom: 1px solid #D8D8D8;

				> div {
					flex: 1;
					max-width: 100%;
					padding-right: 15px;
				}
			}

			&-head {
				min-height: 40px;

				p {
					white-space: nowrap;
					font-size: 14px;
					font-weight: bold;
					line-height: 1.71;
					letter-spacing: 0.27px;
				}
			}

			&-body {
				min-height: 65px;

				&:last-of-type {
					border-bottom: none;
				}

				> div {
					p {
						text-decoration: underline;
						font-size: 16px;
						font-weight: 500;
						line-height: 1.5;
						letter-spacing: 0.31px;
					}

					span {
						font-size: 16px;
						line-height: 1.5;
						letter-spacing: 0.31px;
					}

					a {
						font-size: 16px;
						line-height: 1.5;
						letter-spacing: 0.31px;
						color: #6772e5;
					}
				}
			}
		}
	}

	&__subscribed {

	}
}

// SECTION u-profile :: END
